export default {
	data() {
		return {
			list:[],
			stute:[]
		}
	},
	onLoad() {
		
		var that = this;
		var i = 0;
		var j = 0;
		uni.request({
			url: 'http://106.13.239.202/index.php?c=taxi&m=taxilist',
			method: 'GET',
			data: {	
			},
			success: res => {
				
			  console.log(res.data.data)
			  that.list = res.data.data;
			  j = res.data.data.length;
		       
			   while(i<j)
				  {     
			   if(that.list[i].status == 0 )
				 {  
					that.stute[i] = "未接单";
									 
								 }
				else if(that.list[i].status == 1 )
				{
						that.stute[i] = "已经接单"; 
						
								}	 
							
								
					 i++;
		    }
				   
			},
			fail: () => {
			
			},
			complete: () => {}
		});
	}
	,
	methods: {
		todetails(item)
		{
			var code = item.code;
			
			uni.navigateTo({
				url: `/pages/taxi/details?code=${code}`
			})
		}
	}
}
